  background: var(--footer-bg);
  color: var(--footer-text);
  margin-top: auto;
}
.sxa-footer .footer-main {
  padding: 60px 0 40px;
}
@media (max-width: 768px) {
  .sxa-footer .footer-main {
    padding: 40px 0 30px;
  }
}
.sxa-footer .footer-content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
}
@media (max-width: 992px) {
  .sxa-footer .footer-content {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}
@media (max-width: 576px) {
  .sxa-footer .footer-content {
    grid-template-columns: 1fr;
    gap: 25px;
  }
}
.sxa-footer .footer-column h3.footer-heading {
  margin: 0 0 20px 0;
  font-size: 18px;
  font-weight: 700;
  color: var(--footer-text);
}
.sxa-footer .footer-column .footer-text {
  margin: 0 0 15px 0;
  font-size: 14px;
  line-height: 1.8;
  opacity: 0.9;
}
.sxa-footer .footer-social {
  display: flex;
  gap: 12px;
  margin-top: 20px;
}
.sxa-footer .footer-social .social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  color: var(--footer-text);
  transition: all 0.15s ease-in-out;
}
.sxa-footer .footer-social .social-link:hover {
  background: var(--color-primary);
  transform: translateY(-3px);
}
.sxa-footer .footer-links {
  margin: 0;
  padding: 0;
  list-style: none;
}
.sxa-footer .footer-links li {
  margin-bottom: 12px;
}
.sxa-footer .footer-links li a {
  color: var(--footer-text);
  text-decoration: none;
  font-size: 14px;
  opacity: 0.9;
  transition: all 0.15s ease-in-out;
}
.sxa-footer .footer-links li a:hover {
  opacity: 1;
  color: var(--color-primary);
  padding-left: 5px;
}
.sxa-footer .footer-newsletter {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 15px;
}
.sxa-footer .footer-newsletter .newsletter-input {
  padding: 12px 15px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 0.25rem;
  color: var(--footer-text);
  font-size: 14px;
  transition: all 0.15s ease-in-out;
}
.sxa-footer .footer-newsletter .newsletter-input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}
.sxa-footer .footer-newsletter .newsletter-input:focus {
  outline: none;
  background: rgba(255, 255, 255, 0.15);
  border-color: var(--color-primary);
}
.sxa-footer .footer-newsletter .newsletter-submit {
  padding: 12px 20px;
  background: var(--color-primary);
  border: none;
  border-radius: 0.25rem;
  color: white;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
}
.sxa-footer .footer-newsletter .newsletter-submit:hover {
  background: var(--color-primary-dark);
  transform: translateY(-2px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}
.sxa-footer .footer-bottom {
  padding: 25px 0;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.sxa-footer .footer-bottom .footer-bottom-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 768px) {
  .sxa-footer .footer-bottom .footer-bottom-content {
    flex-direction: column;
    gap: 15px;
    text-align: center;
  }
}
.sxa-footer .footer-bottom .copyright {
  margin: 0;
  font-size: 14px;
  opacity: 0.8;
}
.sxa-footer .footer-bottom .footer-legal {
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 576px) {
  .sxa-footer .footer-bottom .footer-legal {
    flex-direction: column;
    gap: 10px;
  }
}
.sxa-footer .footer-bottom .footer-legal a {
  color: var(--footer-text);
  text-decoration: none;
  font-size: 13px;
  opacity: 0.8;
  transition: all 0.15s ease-in-out;
}
.sxa-footer .footer-bottom .footer-legal a:hover {
  opacity: 1;
  color: var(--color-primary);
}
.sxa-footer[data-variant=simple] .footer-content {
  grid-template-columns: 1fr;
  text-align: center;
}
.sxa-footer[data-variant=simple] .footer-social {
  justify-content: center;
}
.sxa-footer[data-variant=newsletter] .footer-content {
  grid-template-columns: 2fr 1fr;
}
@media (max-width: 768px) {
  .sxa-footer[data-variant=newsletter] .footer-content {
    grid-template-columns: 1fr;
  }
}
.sxa-footer[data-variant=newsletter] .footer-column:last-child {
  grid-column: span 2;
}
@media (max-width: 768px) {
  .sxa-footer[data-variant=newsletter] .footer-column:last-child {
    grid-column: span 1;
  }
