lor: var(--footer-text);\r\n    margin-top: auto;\r\n\r\n    .footer-main {\r\n        padding: 60px 0 40px;\r\n\r\n        @media (max-width: $breakpoint-md) {\r\n            padding: 40px 0 30px;\r\n        }\r\n    }\r\n\r\n    .footer-content {\r\n        display: grid;\r\n        grid-template-columns: repeat(4, 1fr);\r\n        gap: 40px;\r\n\r\n        @media (max-width: $breakpoint-lg) {\r\n            grid-template-columns: repeat(2, 1fr);\r\n            gap: 30px;\r\n        }\r\n\r\n        @media (max-width: $breakpoint-sm) {\r\n            grid-template-columns: 1fr;\r\n            gap: 25px;\r\n        }\r\n    }\r\n\r\n    .footer-column {\r\n        h3.footer-heading {\r\n            margin: 0 0 20px 0;\r\n            font-size: 18px;\r\n            font-weight: $font-weight-bold;\r\n            color: var(--footer-text);\r\n        }\r\n\r\n        .footer-text {\r\n            margin: 0 0 15px 0;\r\n            font-size: 14px;\r\n            line-height: $line-height-lg;\r\n            opacity: 0.9;\r\n        }\r\n    }\r\n\r\n    // Social Links\r\n    .footer-social {\r\n        display: flex;\r\n        gap: 12px;\r\n        margin-top: 20px;\r\n\r\n        .social-link {\r\n            display: flex;\r\n            align-items: center;\r\n            justify-content: center;\r\n            width: 40px;\r\n            height: 40px;\r\n            background: rgba(255, 255, 255, 0.1);\r\n            border-radius: 50%;\r\n            color: var(--footer-text);\r\n            transition: $transition-fast;\r\n\r\n            &:hover {\r\n                background: var(--color-primary);\r\n                transform: translateY(-3px);\r\n            }\r\n        }\r\n    }\r\n\r\n    // Footer Links\r\n    .footer-links {\r\n        margin: 0;\r\n        padding: 0;\r\n        list-style: none;\r\n\r\n        li {\r\n            margin-bottom: 12px;\r\n\r\n            a {\r\n                color: var(--footer-text);\r\n                text-decoration: none;\r\n                font-size: 14px;\r\n                opacity: 0.9;\r\n                transition: $transition-fast;\r\n\r\n                &:hover {\r\n                    opacity: 1;\r\n                    color: var(--color-primary);\r\n                    padding-left: 5px;\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    // Newsletter\r\n    .footer-newsletter {\r\n        display: flex;\r\n        flex-direction: column;\r\n        gap: 10px;\r\n        margin-top: 15px;\r\n\r\n        .newsletter-input {\r\n            padding: 12px 15px;\r\n            background: rgba(255, 255, 255, 0.1);\r\n            border: 1px solid rgba(255, 255, 255, 0.2);\r\n            border-radius: $border-radius-sm;\r\n            color: var(--footer-text);\r\n            font-size: 14px;\r\n            transition: $transition-fast;\r\n\r\n            &::placeholder {\r\n                color: rgba(255, 255, 255, 0.6);\r\n            }\r\n\r\n            &:focus {\r\n                outline: none;\r\n                background: rgba(255, 255, 255, 0.15);\r\n                border-color: var(--color-primary);\r\n            }\r\n        }\r\n\r\n        .newsletter-submit {\r\n            padding: 12px 20px;\r\n            background: var(--color-primary);\r\n            border: none;\r\n            border-radius: $border-radius-sm;\r\n            color: white;\r\n            font-size: 14px;\r\n            font-weight: $font-weight-semibold;\r\n            cursor: pointer;\r\n            transition: $transition-fast;\r\n\r\n            &:hover {\r\n                background: var(--color-primary-dark);\r\n                transform: translateY(-2px);\r\n                box-shadow: $box-shadow;\r\n            }\r\n        }\r\n    }\r\n\r\n    // Footer Bottom\r\n    .footer-bottom {\r\n        padding: 25px 0;\r\n        border-top: 1px solid rgba(255, 255, 255, 0.1);\r\n\r\n        .footer-bottom-content {\r\n            display: flex;\r\n     